
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

main {
  padding: 20px;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgb(253, 143, 0 );
  padding: 20px;
  text-align: left;
}

nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}

nav li {
  display: inline-block;
  margin-right: 20px;
}

nav a {
  color: white;
  text-decoration: none;
}

.app-details {
  display: flex;
  align-items: center;
  margin-left: 10px;
}
.app-details a {
  margin-left: 10px; 
  color: white;
  text-decoration: none;
}

.perfil{
  position: absolute;
  top: 18px;
  right: 70px;
  margin-right: 10px;
}
.link{
  color: white;
  text-decoration: none;
}

.segunda-barra-navegacion {
  background-color: rgb(0, 0, 0);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  padding-left: 30px;
  padding-right: 30px;
}

.seccion-izquierda{
  color: white;
  display: flex;
  align-items: center;
  font-size: xx-large;

} 

.seccion-derecha {
  color: white;
  display: flex;
  align-items: center;
  

}

.seccion-busqueda {
  color: white;
  display: flex;
  align-items: center;
  flex-grow: 5; 
  margin: 0 100px;
  
}

.patito{
  margin-right: 10px;
}

.seccion-derecha img {
  margin-left: 30px;
  width: 20px; 
  height: 20px; 
}

.menu {
  background-color: #ffffff; 
  padding: 5px;
  justify-content: center;
  display: flex;
}

.menu ul {
  font-family: "arial", sans-serif;
  list-style: none;
  display: flex;
  justify-content: space-around;
  margin: 0;
  padding: 0;

}

.menu li {
  display: flex;
  justify-content: center; 
  margin-top: 10px; 
  margin-right: 35px;
  
}

.menu a {
  text-decoration: none;
  color: rgb(7, 7, 7);
  font-weight: bold;
  font-size: 16px;
  padding: 10px 0px; 
  display: block;
}

.menu a:hover {
  background-color: rgb(253, 143, 0 ); 
  color: #ffffff;
  border-radius: 5px;
  padding-left: 10px;
  padding-right: 10px;
}

.submenu {
  display: none; 
  text-align: center;
  position: absolute;
  width: 100px;
  background-color: #ffffff; 
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1); 
  z-index: 3;
}

.comida:hover .submenu {
  display: block;
}

.contenedor-carrusel {
  width: 100%; 
  margin: auto; 
}

.contenedor-inicio {
  position: relative;
  height: 450px;
}

.textoencima {
  position: absolute;
  top: 20%;
  left: 44%; 
  transform: translate(-50%, -50%);
  color: rgb(0, 0, 0);
  font-size: 23px;
  font-weight: bold;
  z-index: 2; 
  background-color: rgba(0, 0, 0, 0); 
  padding: 10px; 
  width: 40%; 
}

.texto-encima {
  position: absolute;
  top: 40%;
  left: 44%; 
  transform: translate(-50%, -50%);
  color: rgb(0, 0, 0);
  font-size: 60px;
  font-weight: bold;
  z-index: 2; 
  background-color: rgba(0, 0, 0, 0); 
  padding: 10px; 
  width: 40%; 
}

.texto-abajo {
  position: absolute;
  top: 69%;
  left: 39%; 
  transform: translate(-50%, -50%);
  color: rgb(0, 0, 0);
  font-size: 35px;
  font-weight: bold;
  z-index: 2; 
  background-color: rgba(0, 0, 0, 0); 
  padding: 10px; 
  width: 30%; 
}

.slick-slide img {
  width: 100%; 
  height: auto; 
  border-radius: 8px; 
}

.boton-abajo {
  position: absolute;
  top: 79%;
  display: block;
  left: 29%;
  margin: 20px auto; 
  padding: 10px 20px;
  font-size: 16px;
  z-index: 2;
  background-color: rgb(253, 143, 0 ); 
  color: #fff; 
  border: none;
  border-radius: 50px;
  cursor: pointer;
  transition: background-color 0.3s; 
}

.boton-abajo:hover {
  background-color: rgb(255, 255, 255); 
  color: rgb(253, 143, 0 );
  border-style: solid;
}

.barra {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px; 
  background-color: #ecf0f1; 
  padding: 10px; 
}

.columna {
  padding: 50px;
  flex: 1; 
  text-align: center; 
  border-left: rgb(253, 143, 0 );
  
}

.columna p {
  margin: -10px; 
  border-left-color:rgb(253, 143, 0 );
}

.columna img {
  width: 50px; 
  height: 50px; 
  margin-bottom: 10px;
}

.ti{
  position: relative;
  left: 4%;
}


.mejores-ofertas {
  margin-top: 20px;
  padding: 40px;
}

.ofertas-columnas {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.oferta {
  text-align: center;
  margin-bottom: 20px;
  width: 200px; 
  border-style: dotted;
  color: #333;
  padding: 20px;
}

.oferta img {
  width: 100%;
  max-width: 150px; 
  height: auto;
}

.boferta {
  width: 200px;
  height: 50px;
  background-color: rgb(0, 0, 0); 
  color: rgb(251, 249, 247);
  border-radius: 5px;
  border-color: rgb(24, 24, 24);
}

.boferta:hover {
  background-color: rgb(62, 60, 60); 
  border-color: rgb(62, 60, 60);
}

.cajita{
  position: relative;
  font-size: 15px;
  padding: 3px;
  top: -20px;
  right: 20px;
  background-color: rgb(253, 0, 0);
  color: #fff;
  width: 100px;
}

.nombres{
  text-align: center;
  font-size: 17px;
  color: rgb(0, 0, 0);
}
.monto{
  position: relative;
  top: -15px;
  color: rgb(139, 11, 11);
}


.pie-de-pagina {
  background-color: rgb(253, 143, 0 );
  color: white;
  padding: 20px;
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
}

.datos,
.metodos-de-pago {
  flex: 1;
  margin-right: 20px;
}

.metodos-de-pago img {
  width: 50px;
  margin-right: 10px;
}

.atencion-al-cliente-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
}

.atencion-al-cliente-form {
  width: 60%;
}

.atencion-al-cliente-form h2 {
  color: #333;
}

.atencion-al-cliente-form p {
  color: #666;
}

.atencion-al-cliente-form label,
.atencion-al-cliente-form textarea,
.atencion-al-cliente-form input {
  display: block;
  margin-bottom: 10px;
  width: 100%;
}

.atencion-al-cliente-form button {
  width: 130px;
  background-color: rgb(253, 143, 0 );
  border-color: rgb(253, 143, 0 );
  color: white;
  padding: 10px;
  border-radius: 30px;
  cursor: pointer;
}

.atencion-al-cliente-form button:hover {
  background-color: rgb(253, 143, 0 );
}

.imagen-container {
  width: 35%;
}

.imagen-container img {
  width: 100%;
  height: auto;
}

.acerca-de-container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: 20px;
}

.acerca-de-imagen img {
  max-width: 100%;
  height: auto;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.acerca-de-contenido {
  max-width: 500px;
}

.comidas-container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
  padding: 50px;
}

.comida-item {
