  height: auto;
  border-radius: 8px;
}

.comida-item h3 {
  margin: 10px 0;
}

.comida-item button {
  background-color: #000000;
  color: white;
  padding: 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}

